<template>
  <div
    id="selfPassenger"
    v-loading="loading"
    element-loading-background="#f8f8f8"
  >
    <el-row>
      <h2>乘客</h2>
      <el-divider></el-divider>
      <el-card
        style="margin: 10px 0; height: 150px; position: relative"
        shadow="hover"
        v-for="i in passenger"
        :key="i"
      >
        <i class="el-icon-close close"></i>
        <div class="col">
          <div class="row" style="width: 100%; justify-content: space-between">
            <div>乘客姓名：张三</div>
            <div>乘客电话：13512345678</div>
          </div>
          <div class="row">
            <div>乘客信息：</div>
          </div>
        </div>
        <div
          style="display: flex; justify-content: flex-end; align-items: center"
        >
          <el-button type="primary" size="mini">编辑</el-button>
        </div>
      </el-card>
      <div
        style="
          width: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
        "
      >
        <el-button
          class="el-icon-plus"
          type="text"
          style="margin-top: 10px; font-size: 20px"
          @click="addPassenger"
          v-loading="addPassengerLoading"
        >
          添加乘客
        </el-button>
      </div>
    </el-row>
  </div>
</template>

<script>
import { set } from "vue";
export default {
  data() {
    return {
      loading: true,
      passenger: 8,
      addPassengerLoading: false,
    };
  },
  methods: {
    addPassenger() {
      this.addPassengerLoading = true;
      setTimeout(() => {
        this.addPassengerLoading = false;
        this.passenger++;
      }, 500);
    },
  },
  activated() {
    console.log("activated");
    setTimeout(() => {
      this.loading = false;
    }, 1000);
  },
  deactivated() {
    console.log("deactivated");
    this.loading = true;
  },
};
</script>

<style scoped>
#selfPassenger {
  padding: 30px 25px;
}
.close {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 20px;
  color: #c0c4cc;
}
.close:hover {
  color: #909399;
}
</style>
